"use client"

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { useLanguage } from '@/lib/i18n'
import { DashboardLayout } from '@/components/dashboard-layout'
import { Plus, Trash2, Edit2 } from 'lucide-react'

interface Candidate {
  id: number
  nom_fr: string
  nom_ar: string
  couleur: string
  photo?: string
  logo?: string
  actif: boolean
}

export default function CandidatesPage() {
  const [user, setUser] = useState<any>(null)
  const [candidates, setCandidates] = useState<Candidate[]>([])
  const [formOpen, setFormOpen] = useState(false)
  const [editingId, setEditingId] = useState<number | null>(null)
  const [formData, setFormData] = useState({ nom_fr: '', nom_ar: '', couleur: '#3b82f6' })
  const router = useRouter()
  const { t, language } = useLanguage()

  // üî• Load Candidates from DB
  const loadCandidates = async () => {
    const res = await fetch("/api/candidats", { credentials: "include" })
    const data = await res.json()
    setCandidates(data)
  }

  // üîê Auth + Load
  useEffect(() => {
  const userData = localStorage.getItem("user")
  if (!userData) return router.push("/login")

  const parsedUser = JSON.parse(userData)

  // üî• Correction essentielle
  if (!parsedUser.role || parsedUser.role.toLowerCase() !== "admin") {
    router.push("/dashboard")
    return
  }

  setUser(parsedUser)
  loadCandidates()
}, [])


  // ‚ûï‚û§ SAVE CANDIDATE (POST / PUT)
  const handleSave = async () => {
    if (!formData.nom_fr || !formData.nom_ar) return alert("Champs requis")

    const method = editingId ? "PUT" : "POST"

    const res = await fetch("/api/candidats", {
      method,
      headers: { "Content-Type": "application/json" },
      credentials: "include",
      body: JSON.stringify(editingId ? { id: editingId, ...formData } : formData)
    })

    if (!res.ok) return alert("‚ùå Erreur lors de l'enregistrement")

    setFormOpen(false)
    setEditingId(null)
    setFormData({ nom_fr: "", nom_ar: "", couleur: "#3b82f6" })
    loadCandidates()
  }

  // ‚ùå Delete (soft-delete DB)
  const handleDelete = async (id: number) => {
    if (!confirm("Supprimer ce candidat ?")) return

    const res = await fetch("/api/candidats", {
      method: "DELETE",
      headers: { "Content-Type": "application/json" },
      credentials: "include",
      body: JSON.stringify({ id })
    })

    if (!res.ok) return alert("Erreur suppression")

    loadCandidates()
  }

  // ‚úè Edit mode
  const handleEdit = (c: Candidate) => {
    setEditingId(c.id)
    setFormData({ nom_fr: c.nom_fr, nom_ar: c.nom_ar, couleur: c.couleur })
    setFormOpen(true)
  }

  if (!user) return null
  return (
    <DashboardLayout user={user}>
      <div className="space-y-6">
        <div className="flex justify-between items-center">
          <div>
            <h1 className="text-3xl font-bold text-white">{t('candidates.title')}</h1>
            <p className="text-slate-400 mt-1">{t('candidates.subtitle')}</p>
          </div>

          <Button
            onClick={() => {
              setFormOpen(true)
              setEditingId(null)
              setFormData({ nom_fr: "", nom_ar: "", couleur: "#3b82f6" })
            }}
            className="bg-blue-600 hover:bg-blue-700"
          >
            <Plus className="w-4 h-4 mr-2" /> {t('common.add')}
          </Button>
        </div>

        {/* FORMULAIRE */}
        {formOpen && (
          <div className="bg-slate-800 border border-slate-700 rounded-lg p-6 space-y-4">
            <h2 className="text-lg font-semibold text-white">
              {editingId ? t("common.edit") : t("common.add")} {t("candidates.title")}
            </h2>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <Input
                placeholder="Nom FR"
                value={formData.nom_fr}
                onChange={e => setFormData({ ...formData, nom_fr: e.target.value })}
                className="bg-slate-700 border-slate-600 text-white"
              />

              <Input
                placeholder="Nom AR"
                value={formData.nom_ar}
                onChange={e => setFormData({ ...formData, nom_ar: e.target.value })}
                className="bg-slate-700 border-slate-600 text-white"
              />

              <div>
                <label className="text-slate-200">Couleur</label>
                <input type="color"
                  value={formData.couleur}
                  onChange={e => setFormData({ ...formData, couleur: e.target.value })}
                  className="mt-1 w-12 h-10 border rounded cursor-pointer"
                />
              </div>
            </div>

            <div className="flex gap-2">
              <Button onClick={handleSave} className="bg-blue-600 hover:bg-blue-700">
                {t('common.save')}
              </Button>

              <Button variant="outline" className="border-slate-600"
                onClick={() => { setFormOpen(false); setEditingId(null) }}>
                {t('common.cancel')}
              </Button>
            </div>
          </div>
        )}

        {/* LIST CANDIDATS */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {candidates.map(c => (
            <div key={c.id} className="bg-slate-800 border border-slate-700 rounded-lg p-6">
              <div className="w-12 h-12 mb-4 rounded" style={{ background: c.couleur }} />

              <h3 className="font-semibold text-white">
                {language === "ar" ? c.nom_ar : c.nom_fr}
              </h3>
              <p className="text-slate-400 text-sm">
                {language === "ar" ? c.nom_fr : c.nom_ar}
              </p>

              <div className="flex gap-2 mt-3">
                <Button size="sm" variant="outline" onClick={() => handleEdit(c)}>
                  <Edit2 size={14}/>
                </Button>
                <Button size="sm" variant="outline" className="border-red-600 text-red-500"
                  onClick={() => handleDelete(c.id)}>
                  <Trash2 size={14}/>
                </Button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </DashboardLayout>
  )
}
